import React from "react";
import style from "./styles.module.scss";
import { useMediaQuery } from "react-responsive";
import { languageText } from "../../../enums/contants";

const Skills = ({ list }: any) => {
  const isTabOrMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const isMobile = useMediaQuery({ maxWidth: 767 });
  const barFullWidth = isTabOrMobile ? "100%" : "70%";

  console.log("isTabOrMobile :", isTabOrMobile);
  return (
    <div className={style.container}>
      <p
        className={style.mySkills}
        style={{ fontSize: isMobile ? "30px" : "40px" }}
      >
        {languageText.mySkillsTitle}
      </p>
      <p className={style.skillDesc}>{languageText.skillsDesc}</p>
      <div className={style.skillsContainer} style={{ width: "90vw" }}>
        {list &&
          list.length > 0 &&
          list.map((item) => {
            const { name, fullBarColor, skillBarColor, parcent } = item;
            return (
              <div className={style.fullBar} style={{ width: barFullWidth }}>
                <div
                  className={style.parcentBar}
                  style={{
                    width: parcent,
                    backgroundColor: fullBarColor,
                  }}
                >
                  <p
                    className={style.skillName}
                    style={{
                      backgroundColor: skillBarColor,
                      width: isTabOrMobile ? "auto" : "140px",
                    }}
                  >
                    {name}
                  </p>
                  <p className={style.parcent}>{parcent}</p>
                </div>
              </div>
            );
          })}
      </div>
    </div>
  );
};

export default Skills;
