import { useState } from "react";
import { useMediaQuery } from "react-responsive";
import style from "./styles.module.scss";
import { languageText } from "../../../enums/contants";
import callIcon from "../../../assets/icons/callIcon.png";
import LinkedInIcon from "../../../assets/icons/linkedInIcon.png";
import Email from "../../../assets/icons/mailIcon.png";
import { fetchgetAPI } from "../../../APICalls/fetchAsync";
import endPoints from "../../../APICalls/endpoints";
import logger from "../../../utils/logger";
import toast, { Toaster } from "react-hot-toast";

const ContactMe = () => {
  const [loading, setLoading] = useState(false);
  const isMobile = useMediaQuery({ maxWidth: 767 });
  const isTabOrMobile = useMediaQuery({ query: "(max-width: 1224px)" });

  const [name, setName] = useState("");
  const [message, setMessage] = useState("");

  const apiFetch = async () => {
    try {
      if (name.length > 0 && message.length > 0) {
        setLoading(true);
        const res = await fetchgetAPI(endPoints.FEEDBACK_URL, {
          mode: "cors",
          Headers: {
            "Access-Control-Allow-Origin": "*",
            "Access-Control-Allow-Headers":
              "Origin, X-Requested-With, Content-Type, Accept",
            "Content-Type": "application/json",
          },
          method: "POST",
          body: JSON.stringify({
            body: `Name: ${name}

                ${message}`,
            title: `Portfolio Message`,
          }),
        });
        setLoading(false);
        logger.log("llllllllll :", res);
        if (res && res.isSuccess) {
          toast.success("Thank you for contacting me");
          setMessage("");
          setName("");
        }
      } else toast.error("Please enter Name/Message");
    } catch (e) {
      setLoading(false);
      logger.log("error while feedback :", e);
    }
  };
  return (
    <div className={`${style.container}`}>
      <p
        className={style.mySkills}
        style={{ fontSize: isMobile ? "30px" : "40px" }}
      >
        REACH OUT TO ME
      </p>
      <p className={style.skillDesc}>
        Thank you for visiting my portfolio. I will be happy to listern from
        you.
      </p>
      <div className={`${style.rowStyle} row`}>
        <div className={`col-sm-12 col-md-6 col-lg-6 ${style.columnStyle}`}>
          <div className={style.contactUsBox}>
            <p className={style.contactME}>CONTACT ME</p>
            <input
              value={name}
              type="text"
              className={`form-control ${style.inputField}`}
              id="name"
              placeholder="Name"
              onChange={(e) => setName(e.target.value)}
            />
            <textarea
              rows={5}
              value={message}
              className={`form-control ${style.inputField}`}
              id="message"
              placeholder="Message"
              onChange={(e) => setMessage(e.target.value)}
            />
            <button
              type="button"
              className={`btn ${style.sendBtn}`}
              onClick={apiFetch}
            >
              {loading ? "Sending..." : "Send"}
            </button>
          </div>
        </div>
        {!isTabOrMobile && (
          <div className={`col-sm-12 col-md-2 col-lg ${style.columnStyle}`}>
            <div className={style.dividedLine} />
          </div>
        )}
        <div className={`col-sm-12 col-md-5 col-lg-5 ${style.columnStyle}`}>
          <div className={style.contactStyle}>
            {languageText.contactList.map((item) => {
              const { type, id, desc } = item;
              return (
                <div className={style.contact}>
                  <div className={style.iconCircle}>
                    {type === "mail" && (
                      <img src={Email} className={style.logo} alt="Call Icon" />
                    )}
                    {type === "linkedIn" && (
                      <img
                        src={LinkedInIcon}
                        className={style.logo}
                        alt="LinkedIn Icon"
                      />
                    )}
                    {type === "number" && (
                      <img
                        src={callIcon}
                        className={style.logo}
                        alt="Number Icon"
                      />
                    )}
                  </div>
                  <div className={style.idDescContainer}>
                    <p className={style.contactDesc}>{desc}</p>
                    <p className={style.idsStyle}>{id}</p>
                  </div>
                </div>
              );
            })}
          </div>
          <Toaster position="bottom-center" reverseOrder={false} />
        </div>
      </div>
    </div>
  );
};

export default ContactMe;
