import Lottie from "react-lottie";
import style from "./styles.module.scss";
import bgBackgroumd from "./../../../assets/animation/76847-pkg-background.json";
import downArrow from "./../../../assets/animation/multi-down-arrow.json";
import { useMediaQuery } from "react-responsive";
import { languageText } from "../../../enums/contants";

const Introduction = (props: any) => {
  const { onClickArrow } = props;
  const isTabOrMobile = useMediaQuery({ query: "(max-width: 1224px)" });
  const bgAnimate = {
    loop: true,
    autoplay: true,
    animationData: bgBackgroumd,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  const downAnimate = {
    loop: true,
    autoplay: true,
    animationData: downArrow,
  };

  return (
    <div className={style.container}>
      <div className={style.overLay} style={{ flex: 1 }}>
        <div
          className={style.centerContent}
          style={
            isTabOrMobile
              ? {
                  width: "80%",
                }
              : { marginLeft: "8vw" }
          }
        >
          <p className={`${style.text} ${style.welcome}`}>
            {languageText.welcome}
          </p>
          <p className={`${style.text} ${style.intro}`}>
            <span className={style.myNameis}>{languageText.myNameIs}</span>
            {"  "}
            <span className={style.name}>{languageText.mukthahar}</span>
            {languageText.introPart1}
          </p>
          <p className={`${style.text} ${style.passionate}`}>
            {languageText.introPart2}
          </p>
          <div className={`${style.downArrow}`} style={{ width: "80vw" }}>
            <a onClick={onClickArrow}>
              <Lottie
                isClickToPauseDisabled
                options={downAnimate}
                width={"40%"}
                style={style.lottie}
              />
            </a>
          </div>
        </div>
      </div>
      <Lottie
        options={bgAnimate}
        height={"100%"}
        width={"100%"}
        style={style.lottie}
      />
    </div>
  );
};

export default Introduction;
