import { useMediaQuery } from "react-responsive";
import style from "./styles.module.scss";

type Props = {
  list: any[];
};

const MyWorks = ({ list }: Props) => {
  const isMobile = useMediaQuery({ maxWidth: 767 });

  return (
    <div className={style.container}>
      <p
        className={style.mySkills}
        style={{ fontSize: isMobile ? "30px" : "40px" }}
      >
        WHAT I HAVE DONE
      </p>
      <p className={style.skillDesc}>Below are the projects I have Worked</p>
      <div className={`${style.myWorkContainer} container`}>
        <div className={`${style.rowStyle} row`}>
          {list &&
            list.length &&
            list.map((item) => {
              const { title, techStack, desc } = item;
              return (
                <div
                  className={`col-sm-12 col-md-6 col-lg-4 ${style.colStyle}`}
                >
                  <div className={`${style.card}`}>
                    <p className={style.title}>{title}</p>
                    <p className={style.techstack}>Tech Stack: {techStack}</p>
                    <p className={style.desc}>{desc}</p>
                  </div>
                </div>
              );
            })}
        </div>
        <p className={style.ownApplication}>
          Additionally I have developed small and medium size applications for
          my own purpose. You can find my applications{" "}
          <a href="https://play.google.com/store/apps/developer?id=Mukthahar+Shaik">
            here
          </a>
        </p>
      </div>
    </div>
  );
};

export default MyWorks;
